.footer{
    width: 100%;
    height: 80vh;
    background-color: black;
    color: white;
    padding-top: 180px;
    position: fixed;
    z-index: -2; 
    bottom: 0;
    left: 0;

}

.footer-info {
    font-size: 35px;
    padding: 30px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footer-info > h1 {
    padding-bottom: 0;    
}

.footer-contact {
    font-size: 22px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footer-sns {
    width: 70%;
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

.sns-links {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;

}

.linkedin, .facebook, .instagram {
    color: white;
    font-size: 35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}


.linkedin:hover, .facebook:hover, .instagram:hover {  
    transition: all 0.3s ease-out;
    transform: scale(1.1);
}

.facebook:hover{
    color: #4144d6;
    border-color: #a30808; 
}

.linkedin:hover {
    color: #0ea853;
    border-color: #c5d43c;    
}

.instagram:hover {
    color: #f09433;
    border-color: #119b18;
}

.design-by {
    font-size: 18px;
    padding-left: 50px;
    color: white;
}



.skill-container {
    height: 50px;
    width: 50px;
    display: inline-block;
    padding: 5px;
    margin-top: 20px;
}

.skill-container > div > a > img {
    height: 30px;
    width: 30px;
    display: inline;
}

.skill-container >div {
    height: 40px;
    width: 40px;
    display: inline;
}

@media only screen and (max-width: 960px) {
    .footer-info {
        text-align: center;
        padding: 0;
        font-size: 20px;
    }

    .footer-contact {
        font-size: 16px;
        padding: 100px 0 30px 0;
        text-align: center;
    }

    .footer-sns {
        grid-template-columns: 1fr;
        text-align: center; 
    }

    .design-by {
        padding-bottom: 40px;
    }

}